<!---->
<template>
  <v-container>
    <v-card elevation="0" class="ma-1">
      <v-subheader></v-subheader>
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <v-subheader class="ma-0 mt-n5 pa-0">
              </v-subheader>
            </v-col>
            <v-col cols="2">
              <v-subheader class="ma-0 mt-n5 pa-0"></v-subheader>
            </v-col>
            <v-col cols="2">
              <v-subheader class="ma-0 mt-n5 pa-0"></v-subheader>
            </v-col>
            <v-col cols="2">
              <v-subheader class="ma-0 mt-n5 pa-0"></v-subheader>
            </v-col>
            <v-col cols="2">
              <v-subheader class="ma-0 mt-n5 pa-0"> </v-subheader>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <div class="ma-0 mt-n5 pa-0 pl-4"></div>
            </v-col>
            <v-col cols="2">
              <v-menu v-model="menu1" :close-on-content-click="false" :nudge-right="40" transition="scale-transition"
                offset-y min-width="auto">
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field class="ma-0 mt-n8 pl-1 pr-1 text_field_size" v-model="fruitValueSproutTreatment.date"
                    readonly v-bind="attrs" v-on="on" outlined dense></v-text-field>
                </template>
                <v-date-picker v-model="fruitValueSproutTreatment.date" @input="menu1 = false"></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="2">
              <v-text-field class="ma-0 mt-n8 pl-1 pr-1 text_field_size" dense hide-details="auto" outlined
                v-model.number="fruitValueSproutTreatment.weight"></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field class="ma-0 mt-n8 pl-1 pr-1 text_field_size" dense hide-details="auto" outlined
                v-model.number="fruitValueSproutTreatment.count"></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-btn class="primary ma-0 mt-n12 pl-1 pr-1" @click="saveUseFruitValueSproutTreatment"></v-btn>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <div class="ma-0 mt-n5 pa-0 pl-4">E-L 2731
              </div>
            </v-col>
            <v-col cols="2">
              <v-menu v-model="menu2" :close-on-content-click="false" :nudge-right="40" transition="scale-transition"
                offset-y min-width="auto">
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field class="ma-0 mt-n8 pl-1 pr-1 text_field_size" v-model="fruitValueELStage.date" readonly
                    v-bind="attrs" v-on="on" outlined dense></v-text-field>
                </template>
                <v-date-picker v-model="fruitValueELStage.date" @input="menu2 = false"></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="2">
              <v-text-field class="ma-0 mt-n8 pl-1 pr-1 text_field_size" dense hide-details="auto" outlined
                v-model.number="fruitValueELStage.weight"></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field class="ma-0 mt-n8 pl-1 pr-1 text_field_size" dense hide-details="auto" outlined
                v-model.number="fruitValueELStage.count"></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-btn class="primary ma-0 mt-n12 pl-1 pr-1" @click="saveUseFruitValueELStage"></v-btn>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <div class="ma-0 mt-n5 pa-0 pl-4"></div>
            </v-col>
            <v-col cols="2">
              <v-menu v-model="menu3" :close-on-content-click="false" :nudge-right="40" transition="scale-transition"
                offset-y min-width="auto">
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field class="ma-0 mt-n8 pl-1 pr-1 text_field_size" v-model="fruitValueBagging.date" readonly
                    v-bind="attrs" v-on="on" outlined dense></v-text-field>
                </template>
                <v-date-picker v-model="fruitValueBagging.date" @input="menu3 = false"></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="2">
              <v-text-field class="ma-0 mt-n8 pl-1 pr-1 text_field_size" dense hide-details="auto" outlined
                v-model.number="fruitValueBagging.weight"></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field class="ma-0 mt-n8 pl-1 pr-1 text_field_size" dense hide-details="auto" outlined
                v-model.number="fruitValueBagging.count"></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-btn class="primary ma-0 mt-n12 pl-1 pr-1" @click="saveUseFruitValueBagging"></v-btn>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <br />
      <br />
      <v-divider class="divider_top" />
      <v-row>
        <v-col cols="3">
          <v-subheader> </v-subheader>
        </v-col>
        <v-col cols="2">
          <v-subheader>{{ fruitValues.burden }} </v-subheader>
        </v-col>
      </v-row>
      <v-divider class="divider_center" />
      <v-row>
        <v-col cols="3">
          <v-subheader> </v-subheader>
        </v-col>
        <v-col cols="2">
          <v-subheader>{{ fruitValues.amount }} </v-subheader>
        </v-col>
      </v-row>
      <v-divider class="divider_center" />
      <v-row>
        <v-col cols="3">
          <v-subheader>/ </v-subheader>
        </v-col>
        <v-col cols="2">
          <v-subheader>{{ fruitValues.count }} </v-subheader>
        </v-col>
      </v-row>
      <v-divider class="divider_bottom" />
    </v-card>
  </v-container>
</template>

<script>
import { useFruitValues, useFruitValueSproutTreatment, useFruitValueELStage, useFruitValueBagging } from "@/api/TopStateGrowth/index"

// const fruitValuesDTOData = {
//   burden: 0.5,	//* 
//   amount: 0.8, //* g/mol
//   count: 0.4, //* //
// };

export default {
  props: {
    shared /** MountController */: { required: true },
    selectedMenu: Object,
    selectedField: Array,
    selectedYears: Array,
    selectedDevices: Array
  },
  components: {
  },
  data() {
    return {
      fieldId: null, // ID
      devicesId: this.$store.getters.selectedData.selectedDevices[0].id,// ID
      year: this.$store.getters.selectedData.selectedYears[0].variable, //

      fruitValues: {
        burden: null,	//* 
        amount: null, //* g/mol
        count: null, //* //
      },
      //
      fruitValueSproutTreatment: {
        date: null, //
        weight: null, //
        count: null, //
      },
      //E-L 2731
      fruitValueELStage: {
        date: null, //
        weight: null, //
        count: null, //
      },
      //
      fruitValueBagging: {
        date: null, //
        weight: null, //
        count: null, //
      },

      //
      menu1: false,
      menu2: false,
      menu3: false,

    };
  },
  mounted() {
    this.shared.mount(this);
    this.getUseFruitValues()
  },
  methods: {
    getUseFruitValues() {
      //
      useFruitValues(this.devicesId, this.year).then((response) => {
        //
        const results = response["data"];
        console.log(this.devicesId);
        console.log(this.year);
        this.fruitValues = results.data
      }).catch((error) => {
        //
        console.log(error);
      })
    },
    saveUseFruitValueSproutTreatment: function () {
      const data = {
        "deviceId": parseInt(this.devicesId),
        "year": this.year,
        "eventId": null,
        ...this.fruitValueSproutTreatment
      }
      console.log("saveUseFruitValueSproutTreatment", data)
      //
      useFruitValueSproutTreatment(data)
        .then((response) => {
          const results = response["data"];
          console.log("results", results)
          this.getUseFruitValues()
        }).catch((error) => {
          //
          console.log(error);
        });
    },
    saveUseFruitValueELStage: function () {
      const data = {
        "deviceId": parseInt(this.devicesId),
        "year": this.year,
        "eventId": null,
        ...this.fruitValueELStage
      }
      ////E-L 2731
      useFruitValueELStage(data)
        .then((response) => {
          const results = response["data"];
          console.log("results", results)
          this.getUseFruitValues()
        }).catch((error) => {
          //
          console.log(error);
        });
    },
    saveUseFruitValueBagging: function () {
      const data = {
        "deviceId": parseInt(this.devicesId),
        "year": this.year,
        "eventId": null,
        ...this.fruitValueBagging
      }
      //
      useFruitValueBagging(data)
        .then((response) => {
          const results = response["data"];
          console.log("results", results)
          this.getUseFruitValues()
        }).catch((error) => {
          //
          console.log(error);
        });
    },
    //
    updateTable() {
      this.getUseFruitValues()
    }
  },
};
</script>

<style lang="scss" scoped>
@import "@/style/common.css";

.divider_top {
  margin-bottom: 20px;
  color: "black";
}

.divider_center {
  margin-top: 20px;
  margin-bottom: 20px;
  color: "black";
}

.divider_bottom {
  margin-top: 20px;
  color: "black";
}

.text_field_size {
  min-width: 60px;
}
</style>
